@import url('https://fonts.googleapis.com/css2?family=Comic+Neue:ital@1&family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}


:root {
    --color-bg: #212121;
    --color-bg-variant: #484848;
    --color-primary: #484848;
    --color-primary-variant: #000000;
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
  /*   --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%; */
}


html {
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    display: none;
}


body {
    font-family: 'Poppins', sans-serif;
    color: var(--color-white);
    line-height: 1.7;
    background: linear-gradient(-45deg, #1a1919, #1d1b1b, #0a0909, #000000);
    background-size: 500% 500%;
    animation: gradient 9s ease infinite;
    height: 200vh;
    }
    
    @keyframes gradient {
    0% {
    background-position: 0% 50%;
    }
    50% {
    background-position: 100% 50%;
    }
    100% {
    background-position: 0% 50%;
    }
    }
/* ============== GENERAL STYLES */

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2,
section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-white);
    margin-bottom: 3rem;
}

.text-ligth {
    color: var(--color-light);
}

a {
    color: var(--color-white);
    transition: var(--transition);
    background-color: var(--color-primary);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-white);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background-color: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary-variant);
    color: var(--color-white);
}
img {
    display: block;
    width: 100%;
    object-fit: cover;
}

 /* ================= MEDIA QUERIES (MEDIUM DEVICES) ================ */

 @media screen and (max-width: 1024px ) {
    .container {
        width: var(--container-width-md);
    }
    section {
        margin-top: 6rem;
    }
 }
 @media screen and (max-width: 600px ) {
    .container {
        width: var(--container-width-sm);
    }
    section > h2  {
        margin-bottom: 6rem;
    }
 }